body {
  background-color: #008b8b;
}

#title {
  color: #ff0;
}

button {
  width: 100%;
  height: 30px;
  background-color: #faebd7;
}

.navbar {
  z-index: 1;
  background-color: #dde3ae;
  flex-flow: row;
  justify-content: center;
  gap: 100px;
  display: flex;
  position: sticky;
  top: 0;
}

.navbar a {
  color: #031102;
  font-size: 50px;
  text-decoration: none;
  transition: all .2s;
}

.navbar a:hover {
  color: #eb0a0a;
}

.img-size {
  width: 200px;
}

.productsContainer {
  width: 54.5%;
  text-align: center;
  flex-wrap: wrap;
  gap: 2px;
  margin: auto;
  display: flex;
}

.productsContainer div {
  width: 200px;
  border: 1px solid #000;
}

.productsContainer div button:active {
  background-color: orange;
}

#cartDisplayTitle {
  color: #000;
}

.cartDisplay {
  text-align: center;
  width: 380px;
  background-color: green;
  border: 2px solid #000;
  position: absolute;
  top: 150px;
  left: calc(50% + 550px);
}

.cartDisplay h2 {
  color: #fff8dc;
}

#shoppingCartTitle {
  color: #000;
}

.shoppingCartText {
  width: 30%;
  text-align: center;
  background-color: green;
  border: 2px solid #000;
  margin: 100px auto auto;
}

.shoppingCartText h2 {
  color: #fff8dc;
}

.shoppingCartText button {
  width: 200px;
  background-color: orange;
}

@media screen and (width <= 1536px) and (height <= 864px) {
  .img-size {
    width: 150px;
  }

  .cartDisplay {
    width: 250px;
    top: 100px;
    left: calc(50% + 450px);
  }
}

@media screen and (width <= 360px) and (height <= 780px) {
  .img-size {
    width: 50px;
  }

  .productsContainer {
    font-size: 30px;
  }

  button {
    width: 200px;
    font-size: 30px;
  }

  .cartDisplay {
    width: 100px;
    font-size: 30px;
    left: 0;
  }
}

